const CuiPullfresh = ()=>import('@/components/cui-vue/cui-pullrefresh/CuiPullrefresh');
import userModel from '@/model/user'
export default {
    name: "UserCenter",
    components:{
      CuiPullfresh
    },
    data()
    {
      return {
        username:"12312",
        headerImg:"",
        sex:"man"
      }
    },
    methods:
    {
      isOk(done)
      {
        let d=done;
        setTimeout(function(){
          let that=this;
          userModel.getUserInfo((ret)=>{
            that.username=ret.username;
            that.headerImg=ret.headerImg;
            that.sex=ret.sex;
            d();
          });
        },2000);
      }
    },
    created()
    {
      let that=this;
      userModel.getUserInfo((ret)=>{
        that.username=ret.username;
        that.headerImg=ret.headerImg;
        that.sex=ret.sex;
      });
    }
}



