export default {
  name: 'app',
  data()
  {
    return {
      transitionName:""
    }
  },
  watch:{
    '$route' (to, from) {

      const toIndex = to.fullPath.split("/")
      const fromIndex = from.fullPath.split("/")
      this.transitionName = toIndex < fromIndex ? 'slide-right' : 'slide-left'
    }
  }
}



