export default {
    name: "CuiLoading",
    data()
    {
        return {

        }
    },
    props:{
        //背景颜色
        bgColor:[],
        //加载的图片
        img:{
            default()
            {
                return require("./loading.gif");

            }
        },
        //是否显示
        isShow:{
            default(){
                return false;
            }
        },
        //提示文本
        msg:{
            default(){
               return "";
            }
        }
    },
    watch:{
      isShow:function(newval)
      {
          if(newval)
          {
              this.$emit("onShow");//当loading被展示时被调用
          }
          else
          {
              this.$emit("onHide");//当loading被隐藏时被调用
          }
      }
    },
    created()
    {

    },
    methods:{}
}



